<template><section><h1>icon 图标</h1>
<p>xl-vision 的图标使用了开源项目<a href="http://ionicons.com" target="_blabk">ionicons</a></p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-icon type=\&quot;locked\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;arrow-swap\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;information\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;help\&quot;&gt;&lt;/xl-icon&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-icon type="locked" style="margin-right:1em;"></xl-icon>
<xl-icon type="arrow-swap" style="margin-right:1em;"></xl-icon>
<xl-icon type="information" style="margin-right:1em;"></xl-icon>
<xl-icon type="help"></xl-icon>
</div>
                            <div slot="desc"><p>通过指定<code>type</code>选择标签</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'locked'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'arrow-swap'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'information'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'help'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zhi-ding-yan-se"><a class="header-anchor" href="#zhi-ding-yan-se" aria-hidden="true">¶</a> 指定颜色</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-icon type=\&quot;locked\&quot; color=\&quot;red\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;arrow-swap\&quot; color=\&quot;blue\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;information\&quot; color=\&quot;yellow\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;help\&quot;&gt;&lt;/xl-icon&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-icon type="locked" color="red" style="margin-right:1em;"></xl-icon>
<xl-icon type="arrow-swap" color="blue" style="margin-right:1em;"></xl-icon>
<xl-icon type="information" color="yellow" style="margin-right:1em;"></xl-icon>
<xl-icon type="help"></xl-icon>
</div>
                            <div slot="desc"><p>通过指定<code>color</code>选择标签</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'locked'</span> <span class="hljs-attr">color</span>=<span class="hljs-string">'red'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'arrow-swap'</span> <span class="hljs-attr">color</span>=<span class="hljs-string">'blue'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'information'</span> <span class="hljs-attr">color</span>=<span class="hljs-string">'yellow'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'help'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zhi-ding-da-xiao"><a class="header-anchor" href="#zhi-ding-da-xiao" aria-hidden="true">¶</a> 指定大小</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-icon type=\&quot;locked\&quot; :size=\&quot;1\&quot; style=\&quot;margin-right:1em;\&quot;&gt;&lt;/xl-icon&gt;\n&lt;xl-icon type=\&quot;arrow-swap\&quot; size=\&quot;20px\&quot;&gt;&lt;/xl-icon&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-icon type="locked" :size="1" style="margin-right:1em;"></xl-icon>
<xl-icon type="arrow-swap" size="20px"></xl-icon>
</div>
                            <div slot="desc"><p>通过指定<code>size</code>选择标签</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'locked'</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">'1'</span> <span class="hljs-attr">style</span>=<span class="hljs-string">'margin-right:1em;'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'arrow-swap'</span> <span class="hljs-attr">size</span>=<span class="hljs-string">'20px'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="icon-shu-xing"><a class="header-anchor" href="#icon-shu-xing" aria-hidden="true">¶</a> icon 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>图标</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>size</td>
<td>图标大小，字符串或者数字，数字时，后缀为 em</td>
<td>string/number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>color</td>
<td>图标颜色</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="icon-shi-jian"><a class="header-anchor" href="#icon-shi-jian" aria-hidden="true">¶</a> icon 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-click</td>
<td>图标点击事件</td>
<td>event原生点击事件</td>
</tr>
</tbody>
</table>
<h2 id="tu-biao-sou-suo"><a class="header-anchor" href="#tu-biao-sou-suo" aria-hidden="true">¶</a> 图标搜索</h2>
<div class="input-search">
    <xl-input placeholder="搜索图标" v-model="value"></xl-input>
</div>
<button class="icon-item" :key="index" v-for="(icon,index) in filters" :data-clipboard-text="`<xl-icon type='${icon.name}'></xl-icon>`">
    <xl-icon :size="3" :type="icon.name"></xl-icon>
    <div class="icon-name">
        <xl-tooltip content="复制图标">
            <p>{{icon.name}}</p>
        </xl-tooltip>
    </div>
</button>


</section></template>
<style>
.icon-item{
    border: none;
    outline: none;
    background: transparent;
    display:inline-block;
    text-align:center;
    width:10em;
    cursor:pointer;
    margin: 1em;
}
.icon-name{
    background-color: #eee;
    border-radius: 3px;
}
.icon-name p {
    margin: 0;
    padding: 0.5em 1em;
}
.input-search{
    margin-bottom:2em;   
}
.input-search .xl-input{
    margin: 0 auto;
    width: 40em;
}
</style>
<script>
    const ClipboardJS = require('clipboard')
    import icons from '../data/icons'
    export default{
        data(){
            return{
                value:'',
            }
        },
        computed:{
            filters(){
                return icons.filter(icon => icon.tag.indexOf(this.value) > -1);
            }
        },
        mounted() {
            this.clipboard = new ClipboardJS('.icon-item')
        },
        beforeDestroy() {
            this.clipboard.destroy()
        }
    }
</script>


